.gists {
  .gists-img {
    position: absolute;
    top: 30px;
    right: 30px;
    width: 60px;
    cursor: pointer;
    filter: grayscale(100%);
    opacity: .5;
    &:hover {
      opacity: 1; } }
  .gists-popap {
    position: absolute;
    top: 90px;
    right: 30px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    z-index: 1000;
    label {
      margin-bottom: 15px;
      font-size: 14px;
      span {
        width: 130px;
        display: inline-block; } } } }
